<template>
  <div>
    <div class="list_box" style="margin-top:10px;">
      <p class="title">房型名称</p>
      <p>{{ item.orderRoom.roomScale.scaleName }}</p>
    </div>
    <div class="list_box">
      <p class="title">房间号</p>
      <p>{{ item.orderRoom.roomSn }}</p>
    </div>
    <div class="list_box">
      <p class="title">用户来源</p>
      <p>{{ orderSourceMap[item.orderRoom.order.orderSource] }}</p>
    </div>
    <div class="list_box">
      <p class="title">订单号</p>
      <p>{{ item.orderRoom.order.orderSn }}</p>
    </div>
    <div class="list_box">
      <p class="title">入离日期</p>
      <p>{{ start }}至{{ end }},共{{ item.orderRoom.order.diffDay }}{{ item.orderRoom.orderType==2?'小时':'晚' }}</p>
    </div>
    <div class="list_box">
      <p class="title">入住人</p>
      <p>{{ item.orderRoom.sysUser?item.orderRoom.sysUser.realName:'无' }}</p>
    </div>
    <div class="list_box" style="margin-top:20px;">
      <p class="title">房费金额</p>
      <p>&yen;{{ item.roomAmount?item.roomAmount:'0' }}</p>
    </div>
    <div class="list_box">
      <p class="title">房间消费</p>
      <p>&yen;{{ item.depositAmount?item.depositAmount:'0' }}</p>
    </div>
    <div class="list_box">
      <p class="title">承担退款</p>
      <p>&yen;{{ item.refuseAmount?item.refuseAmount:'0' }}</p>
    </div>
    <div class="list_box">
      <p class="title">佣金</p>
      <p>&yen;{{ item.platCommission?item.platCommission:'0' }}</p>
    </div>
    <div class="list_box">
      <p class="title">消费劵抵扣</p>
      <p>&yen;{{ item.couponAmount?item.couponAmount:'0' }}</p>
    </div>
    <div class="list_box" style="margin-bottom:10px;font-size:18px;">
      <p class="title">结算金额</p>
      <p style="color:#f60;">&yen;{{ item.balanceAmount }}</p>
    </div>
  </div>
</template>
<script>
import '@/assets/styles/global.css';
import { GetSystemDictDataByType } from '@/api/api_query';
export default {
    data() {
        return {
            item: '',
            orderSourceMap: {},
            start: '',
            end: ''
        };
    },
    created() {
        this.$store.commit('setHeader', '订单房间详情');
        this.getMore();
        this.item = JSON.parse(localStorage.getItem('list_money'));
        if (this.item.orderRoom.orderType ==
 1) {
            this.start = this.item.orderRoom.order.startTime.substr(0, 10);
            this.end = this.item.orderRoom.order.endTime.substr(0, 10);
        }
    },
    methods: {
        // 数据查询
        getMore() {
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let obj = {};
                for (let i = 0; i < res.data.length; i++) {
                    obj[res.data[i].dictValue] = res.data[i].dictLabel;
                }
                this.orderSourceMap = obj;
            });
        }
    }
};
</script>
<style scoped>
.list_box {
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #e5e5e5;
    background-color: #fff;
}
.title {
    color: #888888;
}
</style>



